
import { Options, Vue } from "vue-class-component";
import ChatHistory from "./ChatHistory.vue";

@Options({
  data() {
    return {
      msg: "",
    };
  },
  methods: {
    sendMessage(message: string) {
      this.msg = "";
      this.$store.dispatch("sendMessage", message);
    },
  },
  components: {
    ChatHistory,
  },
})
export default class HomePage extends Vue {}
